/*提示框样式*/
.modal-custom-box{
  position: fixed;
  top:50%;
  left:50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  height:40px;
  padding:0 15px;
  line-height:40px;
  text-align:center;
  z-index:9999;
}
.modal-custom-text{
  display: block;
  border-radius:4px;
  font-size:14px;
  color:#fff;
  padding:0 15px;
  background:#595959;
}
.modal-custom-text.success{
  background:#595959;
}
.modal-custom-text.fail{
  background: #df6877;
}
